<template>
    <div class="edit" v-if="values.length > 0">
        <breadcrumb :name="name" class="bread"></breadcrumb>
        <label for="">
            ID:
            <a class="button"></a>
            <input type="text" name="head" v-model="id" :value="name">
        </label>
        <label for="">
            <p>:</p>
            <input type="file" name="file" @change="getFile($event)" ref="file" value="12" />
            <input type="text" name="fileVal" v-model="thumb">
            <a @click="uploadImg" class="button"></a>
        </label>
        <label for="" v-for="(name,index) in inputs" :key="index" v-show="inputs.length>0">
            {{ name }}
            <textarea v-if="name === '' || name === ''" name="head" id="" cols="30" rows="6" v-model="values[index]" :value="name">
    
            </textarea>
            <input v-if="name === '' || name === ''" type="datetime-local" name="head" v-model="values[index]" :value="values[index]">
            <input v-else type="text" name="head" v-model="values[index]" :value="name">
        </label>
        <div>
            <input type="checkbox" name="show" id="show" v-model="isShow">
            <label for="show"></label>
        </div>
        <div>
            <input type="checkbox" name="show" id="end" v-model="isEnd">
            <label for="end"></label>
        </div>
        <a @click="saveBlog" class="button"></a>
    </div>
</template>

<script>
import breadcrumb from 'components/breadcrumb'
import Vue from 'vue'
import { apiUrl } from 'common/js/dom'
import axios from 'axios'
import wangEditor from 'wangeditor'
let qs = require('qs');
var dateFormat = "";

//datetime-local
function getFormat() {
    dateFormat = "";
    var nTime = new Date();
    dateFormat += nTime.getFullYear() + "-";
    dateFormat += (nTime.getMonth() + 1) < 10 ? "0" + (nTime.getMonth() + 1) : (nTime.getMonth() + 1);
    dateFormat += "-";
    dateFormat += nTime.getDate() < 10 ? "0" + (nTime.getDate()) : (nTime.getDate());
    dateFormat += "T";
    dateFormat += nTime.getHours() < 10 ? "0" + (nTime.getHours()) : (nTime.getHours());
    dateFormat += ":";
    dateFormat += nTime.getMinutes() < 10 ? "0" + (nTime.getMinutes()) : (nTime.getMinutes());
    dateFormat += ":00";
    return dateFormat
}

export default {
    data() {
        return {
            name: 'Edit',
            inputs: [],
            values: [],
            isShow: false,
            isEnd: false,
            file: '',
            thumb: '',
            editor: '',
            editorContent: '',
            singleUrl: '',
            id: 0,
            startTime: {
                time: ''
            }

        }
    },
    watch: {
        values(val) {
            console.log(val)
        },
        selected(val) {
            console.log(val)
        },
        checked(val) {
            console.log(val)
        },
        editorContent(val) {
            console.log(val)
        }
    },
    created() {
        this.addEl()
        this.values[1] = dateFormat
        this.values[2] = dateFormat
        //console.log(this.hasEditor)
    },
    mounted() {
        if (this.singleUrl) {
            this.pushCont()
        }
    },
    components: {
        breadcrumb
    },
    methods: {
        addEl() {
            let data = this.$route.query

            if (data.url) {
                this.singleUrl = data.url
            }

            for (let i = 0; i < data.inputs.length; i++) {
                Vue.set(this.values, i, '')
                Vue.set(this.inputs, i, data.inputs[i])
            }

        },
        getFile(event) {
            this.file = event.target.files[0];
        },
        pushCont() {
            this.$http.get(this.singleUrl, {
                withCredentials: true
            }).then((res) => {
                let info = res.data.data
                let product = info.productCode ? info.productCode : 0
                console.log(info)
                this.id = info.id
                this.thumb = info.thumb
                this.values[0] = info.title
                this.values[1] = info.eventTime
                this.values[2] = info.signupEndTime
                this.values[3] = info.category
                this.values[4] = info.suitablePeople
                this.values[5] = info.content
                this.values[6] = info.address
                this.values[7] = info.lecturer
                this.values[8] = info.lecturerTitle
                this.values[9] = info.lecturerThumb
                this.values[10] = info.lecturerDesc
                this.isShow = info.isShow === 1 ? true : false
                this.isEnd = info.isEnd === 1 ? true : false
            })
        },
        saveBlog() {
            let stateVal = this.isShow ? 1 : 0
            let endVal = this.isEnd ? 1 : 0

            let event = {
                'title': this.values[0],
                'eventTime': this.values[1],
                'signupEndTime': this.values[2],
                'category': this.values[3],
                'suitablePeople': this.values[4],
                'content': this.values[5],
                'address': this.values[6],
                'lecturer': this.values[7],
                'lecturerTitle': this.values[8],
                'lecturerThumb': this.values[9],
                'lecturerDesc': this.values[10]
            }

            let config = {
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                }
            }

            if (this.singleUrl) {
                this.$http.put(`${apiUrl}/admin/patsnap/event`, qs.stringify(event), {
                    withCredentials: true
                }).then(function (res) {
                    console.log(res)
                }).catch((err) => {
                    console.log(err)
                })
            } else {
                this.$http.post(`${apiUrl}/admin/patsnap/event`, qs.stringify(event), {
                    withCredentials: true
                }).then(function (res) {
                    console.log(res)
                }).catch((err) => {
                    console.log(err)
                })
            }

        },
        uploadImg() {
            let formData = new FormData();
            formData.append('file', this.file);

            let _this = this

            let config = {
                headers: {
                    'Content-Type': 'multipart/form-data'
                }
            }
            this.$http.post(`${apiUrl}/admin/file_manager/attachments/uploadImage`, formData, {
                withCredentials: true
            }, config).then(function (res) {
                console.log(res)
                _this.thumb = res.data.data[0]
            }).catch((err) => {
                console.log(err)
                // this.$router.push({
                //     name: 'login'
                // })
            })
        }
    }

};
</script>

<style lang="stylus" rel="stylesheet/stylus">
.edit
    label
        display : block
        width : 100%
        margin : 10px 0
        &.inline
            display : inline-block
            input
                display : inline-block
                width : 60px
        textarea
            width: 100%
            margin : 10px 0
        input
            width : 100%
            height : 30px
            margin : 10px 0
            &[type='file']
                width : 70px
                border: none
            &[name='fileVal']
                display : inline-block
                width : 70%
                margin-right : 20px
        select
            width : 100%
            margin : 10px 0
            height : 30px
            font-size : 18px
    .button
            display: inline-block;
            outline: none;
            cursor: pointer;
            text-align: center;
            text-decoration: none;
            color : #fff
            background : green
            padding: 10px 20px;
            text-shadow: 0 1px 1px rgba(0,0,0,.3);
            border-radius: 6px;
            box-shadow: 0 1px 2px rgba(0,0,0,.2);
            &:hover
                cursor : pointer
                background : #51a351
            &:active
                position: relative
                top: 1px
    #editor        
            h1 
                font-size: 36px
                font-weight : 500
            h2 
                font-size: 30px
                font-weight : 500
            h3 
                font-size: 24px
                font-weight : 500
            h4 
                font-size: 18px
                font-weight : 500
            h5 
                font-size: 14px
                font-weight : 500
            ol
                list-style : decimal
                li
                    list-style : decimal
            ul 
                list-style : disc
                li
                    list-style : disc
</style>


